.player{
    min-height: 20vh;
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content:space-between;
}
.time-controller{
    width:50%;
    display:flex;
    input{
        width:100%;
        -webkit-appearance: none;
        background-color: transparent;
        cursor: pointer;
    }
    p{
        padding:1rem;
    }
}
.play-control{
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding:1rem;
    width:20rem;
    svg{
        cursor:pointer;
    }
}
input[type="range"]:focus {
    outline:none;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height:16px;
    width:16px;    
}
input[type="range"]::-moz-slider-thumb{
    -webkit-appearance: none;
    background:transparent;
    border:none;
}



.track{
    background:rgba(215, 216, 228, 0.753);
    width:100%;
    height:1rem;
    position:relative;
    border-radius:1rem;
    overflow:hidden;
}
.animate-track{
    background:rgb(204, 204, 204);
    position:absolute;
    top:0;
    left: 0;
    height:100%;
    width:100%;
    transform:translateX(0%);
    pointer-events: none;

}

@media screen and(max-width:768px){
    .time-controller{
        width:90%;
    
    }
    .play-control{
        width:60%;
    }
}
